.header {
  padding: 25px 0;
  border-bottom: 1px solid #e9e9e9;
}
.header .logo {
  font-size: 52px;
  font-family: 'Passion One';
  font-weight: 900;
  letter-spacing: 2px;
  line-height: 42px;
  text-transform: uppercase;
}
.main {
  padding: 24px 0;
  display: block;
}
.footer {
  padding: 25px 0;
  text-align: center;
  border-top: 1px solid #e9e9e9;
  background-color: #000;
}
.footer .footer-primary {
  padding: 50px 0 0 0;
}
.footer .footer-copyright {
  padding: 35px 0 0 0;
  color: #e9e9e9;
  font-size: 11px;
  background-color: #000;
}
.menu {
  margin: 0;
  padding: 0;
  display: block;
  border-bottom: 1px solid rgba(255,255,255,0.15);
}
.menu li {
  display: block;
  position: relative;
  -webkit-transition: all 150ms ease-in-out;
  -moz-transition: all 150ms ease-in-out;
  -o-transition: all 150ms ease-in-out;
  -ms-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}
.menu li a {
  padding: 8px 8px 8px 32px;
  display: block;
  color: #efefef;
}
.menu li a:hover {
  text-decoration: none;
}
.menu.mainmenu {
  float: right;
}
.menu.mainmenu ul > li {
  float: left;
}
.menu.mainmenu ul > li > a {
  margin: 0 10px;
  padding: 0 15px;
  display: block;
  color: #000;
  font-size: 13px;
  line-height: 42px;
  text-transform: uppercase;
  -webkit-transition: all 250ms ease-in-out;
  -moz-transition: all 250ms ease-in-out;
  -o-transition: all 250ms ease-in-out;
  -ms-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
}
.menu.mainmenu ul > li:last-child > a {
  margin-right: 0;
}
.menu.mainmenu ul > li:hover a,
.menu.mainmenu ul > li.active a {
  color: #ed6040;
  text-decoration: none;
}
.menu.footermenu {
  width: 100%;
  margin: 0 0 5px 0;
  display: inline-block;
  color: #fff;
  font-family: 'Karla', sans-serif;
  border: 0;
}
.menu.footermenu li {
  display: inline-block;
}
.menu.footermenu li a {
  padding: 0 6px;
}
.menu.footermenu.with-separator li {
  font-size: 10px;
}
.menu.footermenu.with-separator li a {
  display: inline-block;
}
.menu.footermenu.with-separator li:before {
  content: ' | ';
  display: inline-block;
}
.menu.footermenu.with-separator li:first-child:before {
  content: '';
}
.menu.footermenu.primary li a {
  color: #fff;
  font-weight: 100;
  font-size: 14px;
  text-transform: uppercase;
}
.menu.footermenu.secondary {
  color: #b3b3b3;
}
.menu.footermenu.secondary li a {
  color: #b3b3b3;
  font-weight: 100;
  font-weight: 100;
  font-size: 10px;
  text-transform: uppercase;
}
.menu.footermenu.secondary li a:hover {
  color: #fff;
}
.button,
.btn {
  width: auto;
  padding: 4px 8px;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  color: #000;
  font-size: 13px;
  text-align: center;
  text-decoration: none;
  position: relative;
  border-width: 1px;
  border-style: solid;
  border-color: #000;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.1);
  box-shadow: 0 0 5px rgba(0,0,0,0.1);
  background: -webkit-linear-gradient(top, #fff, #ececec);
  background: -moz-linear-gradient(top, #fff, #ececec);
  background: -o-linear-gradient(top, #fff, #ececec);
  background: -ms-linear-gradient(top, #fff, #ececec);
  background: linear-gradient(to bottom, #fff, #ececec);
  -webkit-transition: all 150ms ease-in-out;
  -moz-transition: all 150ms ease-in-out;
  -o-transition: all 150ms ease-in-out;
  -ms-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}
.button.featured,
.btn.featured {
  border-color: #2ca0ba;
  background: -webkit-linear-gradient(top, #3db6d1, #2ca0ba);
  background: -moz-linear-gradient(top, #3db6d1, #2ca0ba);
  background: -o-linear-gradient(top, #3db6d1, #2ca0ba);
  background: -ms-linear-gradient(top, #3db6d1, #2ca0ba);
  background: linear-gradient(to bottom, #3db6d1, #2ca0ba);
}
.button.inverse,
.btn.inverse {
  color: #fff;
}
.button.large,
.btn.large {
  padding: 16px 32px;
  font-size: 16px;
}
.button.highlight,
.btn.highlight {
  margin: 0 auto;
  color: #451206;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  border-color: #e93e17;
  background: -webkit-linear-gradient(top, #ed6040, #e93e17);
  background: -moz-linear-gradient(top, #ed6040, #e93e17);
  background: -o-linear-gradient(top, #ed6040, #e93e17);
  background: -ms-linear-gradient(top, #ed6040, #e93e17);
  background: linear-gradient(to bottom, #ed6040, #e93e17);
}
.button.highlight:hover,
.btn.highlight:hover {
  background: -webkit-linear-gradient(top, #ef7053, #ec5532);
  background: -moz-linear-gradient(top, #ef7053, #ec5532);
  background: -o-linear-gradient(top, #ef7053, #ec5532);
  background: -ms-linear-gradient(top, #ef7053, #ec5532);
  background: linear-gradient(to bottom, #ef7053, #ec5532);
}
.button:hover,
.btn:hover,
.button.active,
.btn.active {
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.15);
  box-shadow: 0 0 5px rgba(0,0,0,0.15);
  background: -webkit-linear-gradient(top, #3db6d1, #2fa9c4);
  background: -moz-linear-gradient(top, #3db6d1, #2fa9c4);
  background: -o-linear-gradient(top, #3db6d1, #2fa9c4);
  background: -ms-linear-gradient(top, #3db6d1, #2fa9c4);
  background: linear-gradient(to bottom, #3db6d1, #2fa9c4);
  text-decoration: none;
}
.button:hover > a,
.btn:hover > a,
.button.active > a,
.btn.active > a {
  color: #fff;
  text-decoration: none;
}
.button-login,
.btn-login {
  background: transparent;
  border-width: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.button-login:hover,
.btn-login:hover {
  color: #fff !important;
  background: #ed6040;
}
.button-play,
.btn-play {
  color: #fff !important;
  background: #3db6d1;
  border-width: 0;
}
.button-play:hover,
.btn-play:hover {
  -webkit-animation: pulse 550ms infinite;
  -moz-animation: pulse 550ms infinite;
  -o-animation: pulse 550ms infinite;
  -ms-animation: pulse 550ms infinite;
  animation: pulse 550ms infinite;
}
.list {
